.about {
  height: 100vh;
  display: flex;
  background-color: #24252a;
}

.about-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.about-image {
  margin-right: 5em;
}

.about-right {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.about-right p {
  font-size: 1.5rem;
  font-weight: 200;
  width: 75%;
}

.about-title {
  font-size: 3rem;
  font-weight: bold;
  color: #59b256;
  margin: 2% 0;
}

.floating {
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 25px);
  }
  100% {
    transform: translate(0, -0px);
  }
}

.icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 30%;
}
