.btn {
  text-align: center;
  font-weight: 400;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
  background-color: #59b256;
  border: none;
  padding: 10px 20px;
  margin-top: 10px;
  font-size: 1rem;
}

.short {
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s ease;
  background-color: #59b256;
  border: none;
  padding: 10px 15px;
  margin-right: 15px;
}

.btn:hover,
.short:hover {
  transform: translateY(-2px);
}
