.project-list {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #24252a;
}

.project-list-title {
  font-size: 3rem;
  font-weight: bold;
  color: #59b256;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  height: 100%;
}
