.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1%;
  background-color: #24252a;
  font-size: 2rem;
  position: sticky;
  top: 0;
  z-index: 1;
}

.navbar-links ul {
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-links li {
  list-style: none;
}

.navbar-links li a {
  transition: all 0.3s ease 0s;
  text-decoration: none;
  padding: 1rem;
}

.navbar-links li a:hover {
  color: #59b256;
}

.toggle-button {
  position: absolute;
  top: 0.75rem;
  right: 1rem;
  display: none;
}

@media (max-width: 600px) {
  .toggle-button {
    display: flex;
  }

  .navbar-links {
    display: none;
  }

  .navbar-links-active ul {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .navbar-links-active li {
    list-style: none;
  }

  .navbar-links-active li a {
    transition: all 0.3s ease 0s;
    text-decoration: none;
    padding: 1rem;
  }

  .navbar-links-active ul {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100vh;
  }
}
