.contact {
  height: 100vh;
  background-color: #24252a;
}

.contact-wrapper {
  display: flex;
  padding: 50px;
  align-content: center;
  gap: 30px;
}

.contact-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.contact-right {
  flex: 1;
  flex-direction: column;
}

.contact-title {
  font-size: 60px;
  margin-top: 10px;
  color: #59b256;
}

form {
  margin-top: 20px;
}

input {
  width: 60%;
  height: 50px;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
  background: #59b256;
  border: none;
  border-bottom: 1px white solid;
}

::placeholder {
  color: white;
}

textarea {
  width: 60%;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
  background: #59b256;
  border: none;
  border-bottom: 1px white solid;
}

.contact-button {
  text-align: center;
  font-weight: 400;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
  background-color: #59b256;
  border: none;
  padding: 10px 20px;
  margin-top: 10px;
  font-size: 1rem;
  display: block;
}

.contact-button:hover {
  transform: translateY(-2px);
}

@media only screen and (max-width: 600px) {
  .contact-wrapper {
    display: flex;
    flex-direction: column;
  }
}
